import {request} from '../../request.js'
export default {
	data() {
		return {
			// 店铺类型(线上/线下)
			typeList: [
				{
					name: '线上',
					value: 0
				},
				{
					name: '线下',
					value: 1
				}
			],
			index:0,
			checked: false,
			// 控制当前处于第几步
			isStatus: 1,
			// 个人
			personChecked: true,
			// 企业
			Companychecked: false,
			currentIndex: 0,
			// 公司类型选择的
			companyList: [
				{
					name: '私营企业',
					value: 0
				},
				{
					name: '个体户',
					value: 1
				},
				{
					name: '外企',
					value: 2
				},
				{
					name: '中外合资',
					value: 3
				}		
			],
			companyIndex: 0,
			// 店铺认证同意radio
			companyChecked: false,
			// 省市区数据
			province: [{'province_name':'请选择'}],
			city:  [{'city_name':'请选择'}],
			district: [{'distrcit_name':'请选择'}],
			provinceId:[],
			cityId:[],
			districtId:[],
			selectProvince:0,
			selectCity:0,
			selectDistrict:0,
			
			// 储存图片前缀
			$api: '',
			// 储存用户的token
			token: '',
			// 储存身份证正面图片
			frontImg: '',
			// 储存身份证反面图片
			backImg: '',
			// 储存手持身份证图片
			shareImg: '',
			//营业执照电子版
			businessImg:'',
			//组织机构代码电子版
			organizationImg:'',
			//一般纳税人证明
			proveImg:'',
			//公司名称
			corporate:'',
			//详细地址
			address:'',
			//姓名
			full_name:'',
			// 用户手机
			user_phone:'',
			// 电子邮箱
			email:'',
			//----个人----
			//身份证号
			idcard:'',
			//银行卡开户名
			bank_username:'',
			//个人银行卡号
			bank_card:'',
			//开户银行名称
			bank_name:'',
			// 支行联行号
			interbank:'',
			//----企业----
			//公司类型
			company_id:0,
			//公司联系电话
			company_phone:'',
			// 公司人数
			company_number:'',
			// 注册资金
			capital:'',
			// 营业执照
			license:'',
			// 法定营业范围
			business_scope:'',
			// 组织机构代码
			organization_code:'',
			//店铺名称
			shop_name:'',
			//店铺等级
			shop_grade_name:'',
			//店铺类型
			shop_type_name:'',
			statusHeight: ''
			
			
			
		};
	},
	onLoad() {
		// 赋值图片前缀
		this.$api = this.$api;
		this.getProvince();
		// 获取当前用户的token
		uni.getStorage({
			key:'token',
			success: res => {
				this.token = res.data[0]
			}
		});
		// 读取当前设备的可视高度
		uni.getSystemInfo({
			success: res => {
				this.statusHeight = res.windowHeight - 150 + 'px';
			}
		})
		
	},
	methods:{
		// 同意协议
		agreeDeal() {
			this.checked = !this.checked
		},
		// 商家协议下一步按钮
		stepOne() {
			if(!this.checked) {
				uni.showToast({
					title: '请先同意相关协议',
					icon: 'none'
				})
				return
			}
			this.isStatus = 2
			
		},
		// 选择线上或线下
		chooseType(event) {
			console.log(event);
			this.index = event.detail.value;
		},
		// 选择个人或企业
		chooseId(event) {
			this.currentIndex = event.detail.value;
			this.personChecked = !this.personChecked;
			this.Companychecked = !this.Companychecked;
		},
		// 返回上一步
		goBack() {
			this.isStatus = 2;
		},
		// 提交审核
		stepThree() {
			if(!this.shop_name) {
				uni.showToast({
					title: '请填写店铺名称',
					icon: 'none'
				})
				return
			}
			if(!this.shop_grade_name) {
				uni.showToast({
					title: '请填写店铺等级',
					icon: 'none'
				})
				return
			}
			if(!this.shop_type_name) {
				uni.showToast({
					title: '请填写店铺类型',
					icon: 'none'
				})
				return
			}
			
			var company_name=this.corporate;
			var company_address_detail=this.address;
			var contacts_name=this.full_name;
			var contacts_phone=this.user_phone;
			var contacts_email=this.email;				
			var company_province_id=this.selectProvince;
			var company_city_id=this.selectCity;
			var company_district_id=this.selectDistrict;
			
			var shop_name=this.shop_name;
			var assistant_code = 0;
			var recommend_uid = 0;
			var shop_type_id=1;
			var shop_group_id=1;
			var shop_type_name=this.shop_type_name;
			var shop_group_name=this.shop_grade_name; 
			
			if(this.personChecked){
					var apply_type = 1;
					var contacts_card_no=this.idcard;
					var contacts_card_electronic_1=this.frontImg;
					var contacts_card_electronic_2=this.backImg;
					var contacts_card_electronic_3=this.shareImg;
					var bank_account_name=this.bank_username;
					var bank_account_number=this.bank_card;
					var bank_name=this.bank_name;
					var bank_code=this.interbank;
				}else{
					var apply_type = 2;
					var company_type=this.company_id
					var company_phone=this.company_phone;
					var company_employee_count=this.company_number
					var company_registered_capital=this.capital
					var business_licence_number=this.license
					var business_sphere=this.business_scope
					var business_licence_number_electronic=this.businessImg
					var organization_code=this.organization_code
					var organization_code_electronic=this.organizationImg
					var general_taxpayer=this.proveImg
				}
				
				request({
					url: "index.php?s=wap/shop/applySecondCompanyInfo",
					method: "POST",
					data: {
						"apply_type":apply_type,
						"company_name":company_name,
						"company_province_id":company_province_id,
						"company_city_id":company_city_id,
						"company_district_id":company_district_id,
						"company_address_detail":company_address_detail,
						"contacts_name":contacts_name,
						"contacts_phone":contacts_phone,
						"contacts_email":contacts_email,
						"contacts_card_no":contacts_card_no,
						"contacts_card_electronic_1":contacts_card_electronic_1,
						"contacts_card_electronic_2":contacts_card_electronic_2,
						"contacts_card_electronic_3":contacts_card_electronic_3,
						"bank_account_name":bank_account_name,
						"bank_account_number":bank_account_number,
						"bank_name":bank_name,
						"bank_code":bank_code,
						"shop_name":shop_name,
						"shop_type_name":shop_type_name,
						"shop_group_name":shop_group_name,
						"shop_type_id":shop_type_id,
						"shop_group_id":shop_group_id,
						"company_type":company_type,
						"company_phone":company_phone,
						"company_employee_count":company_employee_count,
						"company_registered_capital":company_registered_capital,
						"business_licence_number":business_licence_number,
						"business_sphere": business_sphere,
						"business_licence_number_electronic":business_licence_number_electronic,
						"organization_code": organization_code,
						"organization_code_electronic": organization_code_electronic,
						"general_taxpayer": general_taxpayer,
						"recommend_uid": recommend_uid
					}
				}).then(res => {
					this.isStatus = 4
					/* if(res.data.code == 1){
							uni.showToast({
								title: '提交成功',
								icon: 'none'
							});
							
							setTimeout(function(){
								});
					}
					else {
						uni.showToast({
							title: '提交失败',
							icon: 'none'
						});
					} */
				})
			
			
			
			
		},
		
		// 店铺认证
		companyAgree() {
			this.companyChecked = !this.companyChecked
		},
		// 店铺认证下一步按钮
		stepTwo() {
			if(!this.companyChecked) {
				uni.showToast({
					title: '请先同意相关协议',
					icon: 'none'
				})
				return
			}
			if(!this.corporate) {
				uni.showToast({
					title: '请先填写公司名称',
					icon: 'none'
				})
				return
			}
			if(!this.address) {
				uni.showToast({
					title: '请先填写公司地址',
					icon: 'none'
				})
				return
			}
			if(!this.full_name) {
				uni.showToast({
					title: '请先填写姓名',
					icon: 'none'
				})
				return
			}
			if(!this.user_phone) {
				uni.showToast({
					title: '请先填写联系方式',
					icon: 'none'
				})
				return
			}
			
			var myreg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
			if (!myreg.test(this.user_phone)) {
			    uni.showToast({
					title: '请先填写正确的手机号码',
					icon: 'none'
				})
			    return
			}
			
			if(!this.email) {
				uni.showToast({
					title: '请先填写电子邮箱',
					icon: 'none'
				})
				return
			}
			myreg = /^[a-zA-Z0-9_-]+@([a-zA-Z0-9]+\.)+(com|cn|net|org)$/;
			if(!myreg.test(this.email)) {
				uni.showToast({
					title: '请先填写正确的电子邮箱',
					icon: 'none'
				})
				return
			}
			
			
			//---个人---
			if (this.personChecked) {
				if(!this.idcard) {
					uni.showToast({
						title: '请先填写身份证',
						icon: 'none'
					})
					return
				}
				if(!this.bank_username) {
					uni.showToast({
						title: '请先填写银行卡开户名',
						icon: 'none'
					})
					return
				}
				if(!this.bank_card) {
					uni.showToast({
						title: '请先填写银行卡号',
						icon: 'none'
					})
					return
				}
				if(!this.bank_name) {
					uni.showToast({
						title: '请先填写银行名称',
						icon: 'none'
					})
					return
				}
				if(!this.interbank) {
					uni.showToast({
						title: '请先填写支行联行号',
						icon: 'none'
					})
					return
				}
				if(!this.frontImg) {
					uni.showToast({
						title: '请上传身份证正面',
						icon: 'none'
					})
					return
				}
				if(!this.backImg) {
					uni.showToast({
						title: '请上传身份证返面',
						icon: 'none'
					})
					return
				}
				if(!this.shareImg) {
					uni.showToast({
						title: '请上传手持身份证照片',
						icon: 'none'
					})
					return
				}
			}
			
			//企业
			if (this.Companychecked) {
				if(!this.company_phone) {
					uni.showToast({
						title: '请填写公司联系电话',
						icon: 'none'
					})
					return
				}
				if(!this.company_number) {
					uni.showToast({
						title: '请填写员工人数',
						icon: 'none'
					})
					return
				}
				if(!this.capital) {
					uni.showToast({
						title: '请填写注册资金',
						icon: 'none'
					})
					return
				}
				if(!this.license) {
					uni.showToast({
						title: '请填写营业执照',
						icon: 'none'
					})
					return
				}
				if(!this.business_scope) {
					uni.showToast({
						title: '请填写法定营业范围',
						icon: 'none'
					})
					return
				}
				if(!this.organization_code) {
					uni.showToast({
						title: '请填写组织机构代码',
						icon: 'none'
					})
					return
				}
				
				if(!this.businessImg) {
					uni.showToast({
						title: '请上传营业执照号电子版',
						icon: 'none'
					})
					return
				}
				if(!this.organizationImg) {
					uni.showToast({
						title: '请上传组织机构代码证电子版',
						icon: 'none'
					})
					return
				}
				if(!this.proveImg) {
					uni.showToast({
						title: '请上传一般纳税人证明',
						icon: 'none'
					})
					return
				}
				
				
			}
			
			
			this.isStatus = 3
			
		},
		// 上传身份证正面
		chooseFrontImg() {
			this.upLoadImg('frontImg')
		},
		// 上传身份证反面
		chooseBackImg() {
			this.upLoadImg('backImg')
		},
		// 手持身份证照片
		chooseShareImg() {
			this.upLoadImg('shareImg')
		},
		// 营业执照电子版
		chooseBusinessImg() {
			this.upLoadImg('businessImg')
		},
		// 组织机构代码证电子版
		chooseOrganizationImg() {
			this.upLoadImg('organizationImg')
		},
		// 一般纳税人证明
		chooseProveImg() {
			this.upLoadImg('proveImg')
		},
		
		// 上传图片
		upLoadImg(type) {
			uni.chooseImage({
				count: 1,
				success: res => {
					let path = res.tempFilePaths[0];
					// 上传图片
					uni.uploadFile({
						url: this.$api +'index.php?s=/wap/shop/upload_img?token='+this.token,
						filePath: path,
						name: 'cardIdFront',
						success: res => {
							let data = JSON.parse(res.data);
							if(data.code == 1) {
								// 储存图片路径
								switch (type) {
									case 'frontImg':
									this.frontImg = data.patch;
									break;
									case 'backImg':
									this.backImg = data.patch;
									break;
									case 'shareImg':
									this.shareImg = data.patch;
									break
									case 'businessImg':
									this.businessImg = data.patch;
									break
									case 'organizationImg':
									this.organizationImg = data.patch;
									break
									case 'proveImg':
									this.proveImg = data.patch;
									break
								}
								uni.showToast({
									title: '上传成功',
									icon: 'none'
								})
							}
						}
					})
				}
			})
		},
		// 省市区
		bindSelectProvince(e){
			console.log(this.province);
			this.selectProvince = e.detail.value;
			this.getCity();
		},
		bindSelectCity(e){
			this.selectCity = e.detail.value;
			this.getDistrict();
		},
		bindSelectDistrict(e){
			this.selectDistrict = e.detail.value;
		},
		getProvince(){
			let that = this;
			let address_id = this.address_id;
			let address_info = this.address_info;
			request({
				url:'index.php?s=/wap/index/getProvince',
				method:'POST',
			}).then(res=>{
				let province = res.data;
				if(address_id>0){
					for(var i in province){
						if(province[i].province_id==address_info.province){
							that.selectProvince = i;
							console.log(that.selectProvince);
						}
					}
				}
				that.province = province;
				that.getCity();
			});
		},
		getCity(){
			let that = this;
			let address_id = this.address_id;
			let address_info = this.address_info;
			request({
				url:'index.php?s=/wap/index/getCity',
				data:{
					province_id:that.province[that.selectProvince].province_id
				},
				method:'POST',
			}).then(res=>{
				let city = res.data;
				if(address_id>0){
					for(var i in city){
						if(city[i].city_id==address_info.city){
							that.selectCity = i;
							console.log(that.selectCity);
						}
					}
				}
				that.city = city;
				that.getDistrict();
			});
		},
		getDistrict(){
			let that = this;
			let address_id = this.address_id;
			let address_info = this.address_info;
			request({
				url:'index.php?s=/wap/index/getDistrict',
				data:{
					city_id:that.city[that.selectCity].city_id
				},
				method:'POST',
			}).then(res=>{
				let district = res.data;
				if(address_id>0){
					for(var i in district){
						if(district[i].district_id==address_info.district){
							that.selectDistrict = i;
							console.log(that.selectDistrict);
						}
					}
				}
				that.district = district;
			});
		},
		chooseCompanyType(e){
			this.company_id = e.detail.value;
			console.dir(this.company_id);
		}
		
		
		
		
	}
}
